import React from 'react';
import './Style.css';
import Navbar from '../components/Navbar/Navbar';
import { ReactComponent as NameLogo } from '../imgs/name-logo.svg';
import { ReactComponent as Planet } from '../imgs/planet-decoration.svg';

function home() {
    return (
        <div className="fill-window">
            <div class="homeContainer">
                <Navbar />
                <div className="body">
                    <NameLogo className="nameLogo"/>
                </div>
                
            </div>
        </div>
    )
}

export default home;